import * as React from "react";
import { motion, useInvertedScale } from "framer-motion";
import { closeSpring, openSpring } from "./animations";
import { IconButton } from "./IconButton";
import { CopyToClipboard } from "react-copy-to-clipboard";

export const Title = ({
  title,
  platform,
  category,
  isSelected,
  cliCommand,
  id,
  repoUrl,
  demoUrl
}) => {
  const inverted = useInvertedScale();
  const x = isSelected ? 32 : 16;
  const y = isSelected ? 24 : 200;
  const buttonsOpacity = isSelected ? 1 : 0;
  const categoryOpacity = isSelected ? 0 : 0.7;
  const buttonsY = isSelected ? -10 : 0;

  return (
    <motion.div
      className="title-container"
      initial={false}
      animate={{ x, y }}
      transition={isSelected ? openSpring : closeSpring}
      transformTemplate={scaleTranslate}
      style={{ ...inverted, originX: 0, originY: 0 }}
    >
      <h3>{title}</h3>
      <motion.span
        className="category"
        animate={{ opacity: categoryOpacity }}
        initial={false}
        transition={isSelected ? openSpring : closeSpring}
      >
        {platform} &#183; {category}
      </motion.span>
      <motion.div
        className="title-buttons"
        animate={{ y: buttonsY, opacity: buttonsOpacity }}
        transition={isSelected ? openSpring : closeSpring}
        initial={false}
      >
        <CopyToClipboard text={`stripe samples create ${id}`}>
          <IconButton
            text="Start with CLI"
            icon="terminal"
            url=""
            onClick={cliCommand}
          />
        </CopyToClipboard>
        <IconButton
          text="Open on GitHub"
          icon="github"
          url={repoUrl}
          onClick={null}
        />
        <IconButton
          text="View Sample"
          icon="external"
          url={demoUrl}
          onClick={null}
        />
      </motion.div>
    </motion.div>
  );
};

const scaleTranslate = ({ x, y, scaleX, scaleY }) =>
  `scaleX(${scaleX}) scaleY(${scaleY}) translate(${x}, ${y}) translateZ(0)`;
