import * as React from "react";
import { useState } from "react";
import { motion, useInvertedScale } from "framer-motion";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { TestCardData } from "../types";

export const TestCard = React.memo(({ cardNumber, type }: TestCardData) => {
  const [copyButtonText, setCopyButtonText] = useState("Copy");
  const onCopy = () => {
    setCopyButtonText("✓ Copied");
    setTimeout(() => {
      setCopyButtonText("Copy");
    }, 2000);
  };
  return (
    <div className="test-card">
      <h5>{type}</h5>
      <div className="test-card-row">
        <p>{cardNumber}</p>
        <CopyToClipboard text={cardNumber} onCopy={onCopy}>
          <button className="link-button">{copyButtonText}</button>
        </CopyToClipboard>
      </div>
    </div>
  );
});
