import * as React from "react";
import { memo, useRef, useState } from "react";
import { motion, useMotionValue } from "framer-motion";
import { Link } from "react-router-dom";
import { useInvertedBorderRadius } from "../utils/use-inverted-border-radius";
import { CardData } from "../types";
import { TestCards } from "./TestCards";
import { Title } from "./Title";
import { Image } from "./Image";
import { openSpring, closeSpring } from "./animations";
import { useScrollConstraints } from "../utils/use-scroll-constraints";
import { DemoFrame } from "./DemoFrame";

interface Props extends CardData {
  isSelected: boolean;
  history: {
    push: (route: string) => void;
  };
}

const dismissDistance = 150;

export const Card = memo(
  ({
    isSelected,
    id,
    title,
    category,
    platform,
    history,
    backgroundColor,
    repoUrl,
    demoUrl
  }: Props) => {
    const y = useMotionValue(0);
    const zIndex = useMotionValue(isSelected ? 2 : 0);
    backgroundColor = isSelected ? backgroundColor : backgroundColor;

    const inverted = useInvertedBorderRadius(8);

    const cardRef = useRef(null);
    const constraints = useScrollConstraints(cardRef, isSelected);

    const [isCommandShown, setIsCommandShown] = useState(false);

    function toggleCLICommand() {
      setIsCommandShown(!isCommandShown);
    }

    function checkSwipeToDismiss() {
      y.get() > dismissDistance && history.push("/");
    }

    function checkZIndex(latest) {
      if (isSelected) {
        zIndex.set(2);
      } else if (!isSelected && latest.scaleX < 1.01) {
        zIndex.set(0);
      }
    }

    // When this card is selected, attach a wheel event listener
    // const containerRef = useRef(null);
    // useWheelScroll(
    //   containerRef,
    //   y,
    //   constraints,
    //   checkSwipeToDismiss,
    //   isSelected
    // );

    return (
      <li className={`card`}>
        <Overlay isSelected={isSelected} />
        <div className={`card-content-container ${isSelected && "open"}`}>
          <motion.div
            ref={cardRef}
            className="card-content"
            style={{ ...inverted, zIndex, y, backgroundColor }}
            layoutTransition={isSelected ? openSpring : closeSpring}
            drag={isSelected ? "y" : false}
            dragConstraints={constraints}
            onDrag={checkSwipeToDismiss}
            onUpdate={checkZIndex}
          >
            <Image id={id} isSelected={isSelected} />
            <DemoFrame
              id={id}
              isSelected={isSelected}
              isCommandShown={isCommandShown}
              cliCommand={toggleCLICommand}
              demoUrl={demoUrl}
            />
            <Title
              title={title}
              platform={platform}
              category={category}
              isSelected={isSelected}
              cliCommand={toggleCLICommand}
              id={id}
              repoUrl={repoUrl}
              demoUrl={demoUrl}
            />
            <TestCards />
          </motion.div>
        </div>
        {!isSelected && <Link to={id} className={`card-open-link`} />}
      </li>
    );
  },
  (prev, next) => prev.isSelected === next.isSelected
);

const Overlay = ({ isSelected }) => (
  <motion.div
    initial={false}
    animate={{ opacity: isSelected ? 1 : 0 }}
    transition={{ duration: 0.2 }}
    style={{ pointerEvents: isSelected ? "auto" : "none" }}
    className="overlay"
  >
    <Link to="/" />
  </motion.div>
);
