import * as React from "react";
import { motion, useInvertedScale, useAnimation } from "framer-motion";
import { closeSpring } from "./animations";
import { IconButton } from "./IconButton";

export const DemoFrame = ({
  id,
  isSelected,
  demoUrl,
  isCommandShown,
  cliCommand
}) => {
  const inverted = useInvertedScale();
  const progress = useAnimation();

  function frameLoaded(isLoaded) {
    progress.start({
      opacity: 0
    });
  }

  return (
    <motion.div
      className="card-demo-container"
      style={{ ...inverted, originX: 0, originY: 0 }}
      initial={false}
      animate={
        isSelected ? { x: 32, y: 16, opacity: 1 } : { x: 0, y: 184, opacity: 0 }
      }
      transition={closeSpring}
    >
      {isSelected && (
        <>
          <motion.div className="card-demo-loader-container" animate={progress}>
            <motion.div
              className="card-demo-loader"
              animate={{ width: [0, 680, 700] }}
              transition={{ duration: 3 }}
            />
          </motion.div>
          <motion.div
            className="cli-command"
            style={{ pointerEvents: isCommandShown ? "auto" : "none" }}
            animate={
              isCommandShown
                ? { opacity: 1, scale: 1 }
                : { opacity: 0, scale: 1.2 }
            }
            transition={closeSpring}
            initial={false}
          >
            <div className="cli-command-content">
              <h4>✓ Command copied to clipboard</h4>
              <p>
                With the{" "}
                <a href="https://github.com/stripe/stripe-cli" target="_blank">
                  Stripe CLI
                </a>
                , you can bootstrap this sample with your own keys and assets.
              </p>
              <code>$ stripe samples create {id}</code>
              <p>
                <a
                  onClick={() => {
                    cliCommand(false);
                  }}
                >
                  Done
                </a>
              </p>
            </div>
          </motion.div>
          <iframe className="demo-frame" src={demoUrl} onLoad={frameLoaded} />
        </>
      )}
    </motion.div>
  );
};
